"use client";
import Link from "next/link";
import { useState, useEffect } from "react";
import { useAuth } from "@/lib/AuthContext";
import { ShoppingCart } from "lucide-react";

const genres = ["AÃ§Ã£o", "Aventura", "RPG", "Indie", "SimulaÃ§Ã£o", "Esportes"];

export default function Navbar() {
  const { user, logout } = useAuth();
  const [games, setGames] = useState<any[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [searchResults, setSearchResults] = useState<any[]>([]);
  const [showSearch, setShowSearch] = useState(false);
  const [dropdownOpen, setDropdownOpen] = useState(false);

  // Carrinho
  const [cartCount, setCartCount] = useState(0);

  useEffect(() => {
    async function fetchGames() {
      const res = await fetch("http://localhost:4000/games");
      const data = await res.json();
      setGames(data);
    }
    fetchGames();
  }, []);

  useEffect(() => {
    if (!searchTerm.trim()) {
      setSearchResults([]);
      return;
    }
    const filtered = games.filter((g) =>
      g.title?.toLowerCase().includes(searchTerm.toLowerCase())
    );
    setSearchResults(filtered);
  }, [searchTerm, games]);

  // Atualiza a badge do carrinho
  const updateCartCount = () => {
    const savedCart = JSON.parse(localStorage.getItem("cart") || "[]");
    setCartCount(savedCart.length);
  };

  useEffect(() => {
    updateCartCount(); // inicial
    window.addEventListener("storage", updateCartCount); // atualiza quando localStorage muda
    return () => window.removeEventListener("storage", updateCartCount);
  }, []);

  return (
    <nav className="w-full bg-gray-800 p-4 shadow-md fixed top-0 left-0 z-50 flex justify-between items-center h-20">
      {/* Logo */}
      <h1 className="text-2xl sm:text-3xl font-bold text-white">
        <Link href="/">GamersClub</Link>
      </h1>

      <div className="flex items-center space-x-6 text-sm sm:text-base relative">
        {/* Explorar Dropdown */}
        <div
          className="relative"
          onMouseEnter={() => setDropdownOpen(true)}
          onMouseLeave={() => setDropdownOpen(false)}
        >
          <span className="flex items-center text-gray-300 hover:text-blue-400 cursor-pointer">
            Explorar
          </span>
          <div
            className={`absolute top-full left-0 mt-2 w-48 bg-gray-700 rounded-md shadow-lg z-50 transition-all duration-150 ${
              dropdownOpen ? "opacity-100 visible" : "opacity-0 invisible"
            }`}
          >
            {genres.map((genre, i) => (
              <Link
                key={i}
                href={`/games?genre=${genre}`}
                className="block px-4 py-2 text-white hover:bg-gray-600"
              >
                {genre}
              </Link>
            ))}
            <hr className="border-gray-600 my-1" />
            <Link
              href="/promotions"
              className="block px-4 py-2 text-white hover:bg-gray-600"
            >
              PromoÃ§Ãµes
            </Link>
            <Link
              href="/novidades"
              className="block px-4 py-2 text-white hover:bg-gray-600"
            >
              Novidades
            </Link>
          </div>
        </div>

        {/* Minha Biblioteca */}
        {user && (
          <Link href="/library" className="text-gray-300 hover:text-blue-400">
            Minha Biblioteca
          </Link>
        )}

        {/* Pesquisa */}
        <div className="relative">
          <input
            type="text"
            placeholder="Buscar jogos..."
            className="px-3 py-1 rounded-md text-black w-32 sm:w-48 focus:outline-none"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onFocus={() => setShowSearch(true)}
            onBlur={() => setTimeout(() => setShowSearch(false), 150)}
          />
          {showSearch && searchResults.length > 0 && (
            <div className="absolute top-full left-0 w-full bg-gray-700 rounded-md shadow-lg mt-1 z-50 max-h-60 overflow-y-auto">
              {searchResults.map((g) => (
                <Link
                  key={g.id}
                  href={`/games/${g.id}`}
                  className="block px-4 py-2 text-white hover:bg-gray-600"
                >
                  {g.title}
                </Link>
              ))}
            </div>
          )}
        </div>

        {/* Carrinho */}
        <Link href="/cart" className="relative">
          <ShoppingCart className="w-6 h-6 text-gray-300 hover:text-blue-400 transition" />
          {cartCount > 0 && (
            <span className="absolute -top-2 -right-2 bg-red-600 text-white text-xs font-bold px-2 py-0.5 rounded-full">
              {cartCount}
            </span>
          )}
        </Link>

        {/* Conta / Login */}
        {user ? (
          <div className="relative group">
            <span className="text-gray-300 hover:text-blue-400 cursor-pointer">
              OlÃ¡, {user.nickname || user.name} ðŸ’° â‚¬{user.balance?.toFixed(2) || "0"}
            </span>
            <div className="absolute right-0 mt-2 w-48 bg-gray-700 rounded-md shadow-lg z-50 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
              <Link
                href="/profile"
                className="block px-4 py-2 text-white hover:bg-gray-600"
              >
                Editar Perfil
              </Link>
              <Link
                href="/add-funds"
                className="block px-4 py-2 text-white hover:bg-gray-600"
              >
                Adicionar Fundos
              </Link>
              <Link
                href="/security"
                className="block px-4 py-2 text-white hover:bg-gray-600"
              >
                SeguranÃ§a
              </Link>
              <button
                onClick={logout}
                className="w-full text-left px-4 py-2 text-white hover:bg-gray-600"
              >
                Logout
              </button>
            </div>
          </div>
        ) : (
          <>
            <Link href="/register" className="text-gray-300 hover:text-blue-400">
              Registrar
            </Link>
            <Link href="/login" className="text-gray-300 hover:text-blue-400">
              Login
            </Link>
          </>
        )}
      </div>
    </nav>
  );
}
